body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Panel {
  background-color: white;
  height: 500px;
  width: 600px;
  padding: 20px;
  opacity: 0.85;
  border-radius: 5px;
  box-shadow: 0px 0px 4px gray;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.Panel.Inactive {
  width: 450px;
  opacity: 0.5;
}

.PanelInput {
  height: 60%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

input {
  font-size: medium;
  padding: 15px;
  display: block;
  border: 0px;
  border-bottom: 2px solid lightgray;
  background: linear-gradient(90deg, black 50%, rgb(53, 53, 53));
  color: white;
  border-image-source: linear-gradient(
    to right,
    lightgray 0% 33.33%,
    gold 33.33% 66.66%,
    lightgray 66.66% 100%
  );
  border-image-slice: 0 0% 1 67%;
}

input:focus {
  outline: none;
  transition: all 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
  border-image-slice: 0 34% 1 34%;
}

.error {
  background: brown;
}

button {
  outline: none;
  display: block;
  margin: 0 auto;
  padding: 25px 200px;
  background-image: linear-gradient(
    to bottom right,
    lightgray 0%,
    white 30%,
    lightgray 100%
  );
  color: grey;
  border-radius: 5px;
  font-size: x-large;
  font-weight: 600;
  text-transform: uppercase;
}

button:hover {
  color: black;
  transition: all 200ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

h1 {
  text-align: center;
  text-decoration: overline;
  text-transform: uppercase;
}

p {
  padding: 10px;
  text-align: center;
}

.Tire {
  background: radial-gradient(
    circle,
    rgba(126, 126, 126, 1) 0%,
    rgba(205, 205, 205, 1) 50%,
    rgba(171, 171, 171, 1) 100%
  );
  border: 10px solid black;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation-name: rotation;
          animation-name: rotation;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  position: relative;
}

.Spoke {
  height: 100%;
  width: 3px;
  margin: auto;
  background-color: black;
}

.Spoke45 {
  height: 100%;
  background-color: black;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

@-webkit-keyframes rotation {
  0% {
    left: 25%;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  50% {
    left: 75%;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    left: 25%;
  }
}

@keyframes rotation {
  0% {
    left: 25%;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  50% {
    left: 75%;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    left: 25%;
  }
}

.App{
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

}

.Background {
  width: 100%;
  height: 100%;
  background-image: url(/tire/static/media/bg.c015e18e.jpg), radial-gradient(transparent 0%, black 100%);
  background-blend-mode: darken;
  -webkit-filter: blur(1px);
          filter: blur(1px);
  position: absolute;
}
